<template>
  <h1>hello</h1>
</template>

<script>
export default {
  data() {
    return {
      resposta: ""
    };
  },
  mounted() {
    const teste = new XMLHttpRequest();
    teste.onreadystatechange = () => {
      if (teste.readyState === 4 && teste.status === 200) {
        this.resposta = teste.responseText;
      }
    };
    teste.open("GET", "http://localhost:8000/api/", true);
    teste.send();
  }
};
</script>